@charset "UTF-8";
/* リセット
---------------------------------------------------- */
@import "mq";

*:focus{
  outline: none;
}

body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,textarea,p,blockquote,th,td{
	margin:0;
	padding:0;
}

table{
	border-collapse:collapse;
	border-spacing:0;
	font-size:100%;
}
fieldset,img{
	border:0;
	vertical-align: top;
}
address,caption,cite,code,dfn,em,strong,small,th,var{
	font-style:inherit;
	font-weight:inherit;
}

li{
	list-style:none;
}

caption,th{
	text-align:left;
}

h1,h2,h3,h4,h5,h6{
	font-size:100%;
	font-weight:normal;
}

q:before,q:after{content:'';
}

abbr,acronym{
	border:0;
	font-variant:normal;
}

br{
	letter-spacing: normal;
}

/* to preserve line-height and selector appearance */
sup{
	vertical-align:text-top;
}

sub{
	vertical-align:text-bottom;
}
input,textarea,select{
	font-family:inherit;
	font-size:inherit;
	font-weight:inherit;
}
/*to enable resizing for IE*/
input,textarea,select{
	*font-size:100%;
}

.hover{
}

/* ページ共通
---------------------------------------------------- */
